#scrollBtn {
  display: none;
  position: fixed;
  bottom: 15px;
  right: 15px;
  width: 50px;
  height: 70px;
  font-size: 13px;
  text-align: center;
  line-height: 70px;
  z-index: 100;
  border: none;
  outline: none;
  background-color: var(--color-primary);
  color: var(--color-heading);
  cursor: pointer;
  border-radius: 15px;
  z-index: 9999999;
}
#scrollBtn i {
  transform: translateY(-8px);
  transition: all 300ms ease;
}

#scrollBtn:hover i {
  transform: translateY(0);
}

/* Preloader overlay */
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-content-white);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999999;
}
#preloader img {
  width: 70px;
  height: auto;
  animation: scalePulse 1.5s infinite ease-in-out;
  filter: brightness(0);
}

.preloader-wrapper {
  overflow: hidden;
}

@keyframes scalePulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.video-popup {
  position: absolute;
  bottom: 22px;
  right: 73px;
  font-size: 16px;
  line-height: 26px;
  color: var(--color-content-black2);
  font-weight: 600;
  cursor: pointer;
}
.video-popup:hover {
  color: var(--color-content-black2);
}
.video-popup:hover i {
  background-color: #adadad;
}
.video-popup:hover i::after {
  border-color: transparent;
}
.video-popup i {
  position: relative;
  width: 28px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  font-size: 12px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 12px;
  transition: all 0.3s ease;
}
.video-popup i::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 1px solid;
  clip-path: polygon(0 0, 100% 0, 100% 54%, calc(100% - 1px) 54%, 25% 100%, calc(25% - 0.5px) 100%, 0 100%, 0 0);
  -webkit-border-radius: inherit;
  border-radius: inherit;
  -webkit-transition: border-color 0.3s;
  transition: border-color 0.3s;
  border-color: #adadad;
}

.video-modal {
  display: none;
  opacity: 0;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 9999999999999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  transition: opacity 0.3s ease;
}
.video-modal.show {
  display: flex;
  opacity: 1;
}
.video-modal .video-modal-content {
  position: relative;
  width: 1675px;
  height: 800px;
}
@media (max-width: 1440px) {
  .video-modal .video-modal-content {
    width: 100%;
    height: 100%;
    padding: 100px;
  }
}
@media (max-width: 577px) {
  .video-modal .video-modal-content {
    padding: 50px;
    height: 600px;
  }
}
@media (max-width: 390px) {
  .video-modal .video-modal-content {
    height: 500px;
  }
}
.video-modal .video-modal-content iframe {
  width: 100%;
  height: 100%;
  border: 0;
}
.video-modal .video-modal-content .close-btn {
  position: absolute;
  top: -70px;
  right: -98px;
  font-size: 35px;
  color: white;
  cursor: pointer;
  z-index: 99999999;
}
@media (max-width: 1440px) {
  .video-modal .video-modal-content .close-btn {
    top: 8px;
    right: 20px;
  }
}

.pagination {
  justify-content: flex-end;
  margin-top: 70px;
}
@media (max-width: 577px) {
  .pagination {
    margin-top: 56px;
  }
}
.pagination .page-item {
  border: 0;
  margin: 0 0 0 12px;
}
.pagination .page-item .page-link {
  padding: 0 20px;
  height: 51px;
  line-height: 50px;
  text-align: center;
  border-radius: 10px;
  background-color: unset;
  transition: all 0.3s ease;
  font-size: 14px;
  font-weight: 500;
  font-family: var(--font-secondary);
  color: var(--color-content-black2);
  border: 0;
}
.pagination .page-item .page-link::before, .pagination .page-item .page-link::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: var(--radius-lg);
  transition: var(--transition-1);
}
.pagination .page-item .page-link::before {
  content: "";
  position: absolute;
  padding: 1px; /* border ki width */
  border-radius: 12px;
  background: linear-gradient(90deg, #4875c8, #7061c8);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
.pagination .page-item .page-link:hover {
  background: linear-gradient(45deg, #7061c8, #4875c8);
  color: var(--color-content-white);
}
.pagination .page-item .page-link.v1 i {
  font-size: 10px;
  margin-left: 13px;
}

